import { useSelector } from "react-redux"
import Sider from "./Sider"
function Tapahtumat(){
const events = useSelector(state => state.events)


const event = events.map(x =>{
    return(
        <div key={x.id}>
            <h3 style={{padding:"10px"}}>{x.text}</h3>
        </div>
    )
})
const show = <h2>Tll hetkell ei ole tulossa tapahtumia tarkasta uudestaan myhemmin tai ky katsomassa
    lytyyk <a href="https://facebook.com/hyvinvointikeskusluxus/">facebook</a>/<a href="https://instagram.com/hyvinvointikeskusluxus/">instagram</a> sivuilta tulevia tapahtumia.
</h2>

    return(
        <>
        <div className="etusivu2">
    {events.length === 0 ? show : event}   
        </div>
            <Sider />
            </>
    )
}
export default Tapahtumat